.cuadrado {
    text-align: center;
    background-color: rgb(12, 243, 12);
    border: none;
    border-radius: 10px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10em;
    width: 500px;
    height: 20em;
}

.botongencon {
    width: 150px;
    height: 50px;
    border: none;
    border-radius: 10px;
}

.botongencon:hover{
    background-color: rgb(225, 225, 225);
}

@keyframes buttonClick {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(0.9);
    }
    100% {
      transform: scale(1);
    }
  }
  
  .botongencon:active {
    animation: buttonClick 0.6s ease; /* Aplicar la animaci√≥n al hacer clic */
  }
